.form_container {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 0 auto;
    text-align: start;
    border: 1px solid black;
}

.add_button {
    font-weight: bold;
    background-color: white;
    border-radius: 4px;
    border: 0.5px solid grey;
    margin-left: 10px;
    justify-content: left;
    margin-bottom: 20px;
    cursor: pointer;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.add_button:hover,
.add_button:focus {
    color: rgb(240, 58, 240);
}

.input {
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: start;
}

.form_label {
    margin: 0;
    margin-top: 10px;
    margin-left: 10px;
}
